<template>
  <div>
   <div class="rankBody">
    <v-scroll>
      <ul class="rankUl">
        <li class="rankLi" v-for="item in rankList" @click="_selectItem(item)">
          <img class="rank-li-img" :src="item.picUrl">
          <ol class="rank-li-ol">
           <li class="rank-ol-li ellipsis" v-for="(song, index) in item.songList">
            <span> {{ index + 1 }}</span><span> {{ song.songname }}&nbsp;-&nbsp;{{ song.singername }}</span>
          </li>
        </ol>
      </li>
    </ul>
    <div id="footer" v-if="rankList.length > 0">
    <span class="author">我是有底线的</span> <span class="email">email：530366891@qq.com</span>
    </div>
    <div id="footer" v-if="rankList.length <= 0">正在努力获取数据</div>
  </v-scroll>
</div>
<router-view></router-view>
</div>
</template>
<script>
  import VScroll from 'base/scroll/scroll'
  import {getRankList} from 'api/rank'
  import {ERR_OK} from 'api/config'
  import {mapMutations} from 'vuex'
  export default{
    data() {
      return {
        rankList: []
      }
    },
    created() {
      this._getRankList()
    },
    methods: {
      _getRankList() {
        getRankList().then((res) => {
          if (res.code === ERR_OK) {
            this.rankList = res.data.topList
          }
        })
      },
      _selectItem(item) {
        this.$router.push({
          path: `/rank/${item.id}`
        })
        this.setTopList(item)
      },
      ...mapMutations({
        setTopList: 'SET_TOP_LIST'
      })
    },
    components: {
      VScroll
    }
  }
</script>
<style type="text/css">
	.rankBody{
		position: absolute;
    top: 88px;
    bottom: 0;
    left: 0;
    right: 0;
    min-width: 320px;
    overflow: hidden;
    background:#222;
    z-index: 2;
  }
  .rankUl{
    width: 100%;
    padding-top: 20px;
  }
  .rankLi{
    width: 90%;
    height: 100px;
    background: #333;
    margin: 20px 5% 0 5%;
    overflow: hidden;
  }
  .rankLi:first-child {
    margin: 0 5% 0 5%;
  }
  .rank-li-img{
    display: inline-block;
    width: 100px;
    height: 100%;
    max-width: 33%; 
    background: #222;
  }
  .rank-li-ol{
    display: inline-block;
    height: 100%;
    max-width: 65%;
    overflow: hidden;
  }
  .rank-ol-li{
    height: 33%;
    width: 100%;
    color: #999;
    color: hsla(0,0%,100%,.3);
    font-size: 12px;
    line-height: 33px;
    text-align: left;
    text-indent: 15px;
    overflow: hidden;
  }
</style>


